@import url(https://fonts.googleapis.com/css?family=Rye&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
.login-page {
  display: flex;
  justify-content: center;
  height: 90vh;
  background-color: #e7efe1;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form {
  display: flex;
  flex-direction: column;
}

.page-waiter {
  display: flex;
  flex-direction: column;
  background-color: #e7efe1;
  height: 90vh;
}

.main-waiter {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  height: 90vh;
}

.request-title {
  font-size: 30px;
  margin-bottom: 40px;
}

.deliver {
  width: 38vw;
  height: 50vw;
  max-height: 500px;
  background-color: #fff;
  overflow-y: scroll;
  overflow-x: hidden;
}

::-webkit-scrollbar {
  width: 3px;
}

::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background: #ab546559;
}

.deliver-header {
  background-color: #ab5466;
  width: 100%;
  height: 10%;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 19px;
  font-weight: 800;
  color: #fff;
}

.requests {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  font-size: 19px;
}

.requests:not(:last-child) {
  border-bottom: 1px solid #e6e6e6;
  padding-bottom: 5px;
}

.new-request {
  text-align: center;
  justify-content: space-between;
}

.warning {
  border: 1px solid #ffcc4d;
}

.late {
  border: 1px solid #f57970;
}

.ok {
  border: 1px solid #7ed957;
}

.manager {
  display: flex;
  flex-direction: row;
  background-color: #e7efe1;
  overflow-x: scroll;
  overflow-y: hidden;
  height: 90vh;
}

.restaurant-manager {
  margin: 50px 0px 0px 30px;
  background-color: #fff;
  height: 70%;
  width: 300px;
  max-width: 300px;
  min-width: 250px;
  display: flex;
  flex-direction: column;
  padding: 15px
}

.info {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.request {
  height: 75vh;
}

.product {
  margin-top: 15px;
}

.product:not(:last-child) {
  border-bottom: 1px solid #e6e6e6;
  padding-bottom: 5px;
}

.button {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.timer {
  display: flex;
  align-items: center;
  width: 30%;
  justify-content: space-between;
}

.options-extras {
  margin-left: 15px;
  color: #747474
}

.lateMessage {
  color: #f57970;
}

.page-client{
  display: flex;
  justify-content: center;
  height: 90vh;
  background-color: #e7efe1;  
  align-items: center;
  flex-direction: column;
}

.client-info{
  display: flex;
  flex-direction: column;
}

.counter {
  display: flex;
  height: 40px;
  justify-content: space-between;
  text-align: center;
  margin-top: 10px;
}

.counter-button {
  width: 40px;
  border-radius: 100%;
  color: #fff;
  background-color: #737373;
  font-size: 20px;
}

.delete-button {
  margin: 0px 20px 0px 100px;
}

.page-menu {
  display: flex;
  justify-content: center;
  height: 90vh;
  background-color: #e7efe1;
  align-items: center;
}

.menu-title {
  text-align: center;
  height: 10%;
  display: flex;
  align-items: center;
  font-size: 20px;
}

.menu-container-father {
  display: flex;
  justify-content: space-between;
  height: 90vh;
  align-items: center;
  flex-direction: column;
}

.menu-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 0 10px;
  height: 100%;
  background-color: #34b6a6;
}

.product-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 150px;
  max-width: 150px;
  height: 200px;
  max-height: 200px;
  margin: 20px 10px 0px 20px;
  justify-content: space-between;
  border: 4px solid #34b6a6;
  border-radius: 7px;
}

.menu-list {
  width: 73%;
  height: 529px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  background-color: #fff;
  overflow-y: scroll;
}

.product-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.product-image {
  max-width: 110px;
  max-height: 120px;
  display: flex;
  align-items: center;
  background-color: #fff;
  height: 70%;
}

.product-image img {
  width: 100%;
  max-height: 100%;
}

.name-and-price {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 30%;
  background-color: #737373;
}

.name-and-price-text {
  font-size: 15px;
  text-align: center;
  color: #fff !important;
}

.resume-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.title-resume {
  text-align: center;
  margin-bottom: 30px;
}

.request-resume-container {
  display: flex;
  flex-direction: row !important;
}

.request-line {
  display: flex;
  flex-direction: row !important;
  justify-content: space-between;
}

.request-line:not(:last-child) {
  border-bottom: 1px solid #d1cece;
  padding-bottom: 5px;
}

.resume-footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0px 0px 20px 0px;
}

.total {
  display: flex;
  align-self: flex-start;
  padding-left: 15px;
  margin-bottom: 20px;
}

.header {
  width: 100vw;
  height: 10vh;
  background-color: #ab5466;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.title {
  font-family: 'Rye', cursive;
  color: #fff;
}

.logo-container {
  display: flex;
  flex-direction: row;
  width: 100px;
  justify-content: center;
  align-items: center;
}

.logo {
  width: 55px;
}

.logout {
  width: 50px;
}

.button-resume {
  background-color: #ab5466;
  color: #fff;
  font-size: 20px;
  height: 50px;
}

* {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  color: #545454;
};

